.login_wapper{
    width: 100%;
    height: 100%;
    padding: 2rem 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.login_wapper > .login_container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: #ffffff;
    border: 1px solid rgb(224, 218, 218);
    padding: 2rem 2rem;
    border-radius: 4px;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px;
}

.login_wapper > .login_container > h3{
    margin-bottom: 1.5rem;
    letter-spacing: 0.7px;
    font-weight: 500;
}

.login_wapper > .login_container > form{
    display: flex;
    flex-direction: column;
}

.login_wapper > .login_container > form >.errorMsg{
    font-size: small;
    color: #cc0000;
    margin-bottom: 1px;
    margin-left: 1px;
}

.login_wapper > .login_container > form > input{
    width: 17rem;
    height: 2.3rem;
    margin-top: 1rem;
    outline: none;
    padding: 0 1rem;
    border-radius: 5px;
    border: 1px solid rgb(185, 184, 184);
}


input::-webkit-calendar-picker-indicator {
    cursor: pointer;
}

.login_wapper > .login_container > form > input::placeholder{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 14px;
    text-transform: capitalize;
}

.login_wapper > .login_container > form > #date::placeholder{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 14px;
    text-transform:initial;
}


.login_wapper > .login_container > form > .button{
    background-color: rgb(31, 147, 224);
    color: #ffffff;
    cursor: pointer;
    letter-spacing: 0.8px;
    font-size: 15px;
    font-weight: 500;
}
