
.header_wapp {
  width: 100%;
  height: 33.5rem;
  display: flex;
  position: relative;
}

.header_wapp>.header_left {
  height: 100%;
  width: 60%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.header_wapp>.header_left>.header_left_content {
  height: 17rem;
  width: 41rem;
  position: absolute;
  /* background-color: red; */
  top: 33%;
  left: 5rem;
}

.header_wapp>.header_left>.header_left_content>.dynamic-txts {
  height: 70px;
  width: 650px;
  background-color: #ffffff;
  line-height: 90px;
  user-select: none;
  overflow: hidden;
}

.header_wapp>.header_left>.header_left_content>.dynamic-txts>li {
  list-style: none;
  color: #2366bd;
  font-weight: 500;
  position: relative;
  top: 0;
  padding: 0;
  animation: slide 12s steps(4) infinite;
}

@keyframes slide {
  100% {
    top: -360px;
  }
}

.header_wapp>.header_left>.header_left_content>.dynamic-txts>li>span {
  position: relative;
  margin: 5px 0;
  height: 90px;
  width: 100%;
  letter-spacing: 0.4rem;
  background-color: #ffffff;
  font-size: 45px;
  line-height: 4rem;
}

.header_wapp>.header_left>.header_left_content>.dynamic-txts>li span::after {
  content: "";
  position: absolute;
  left: 0;
  height: 94px;
  width: 100%;
  overflow: hidden;
  background: #ffffff;
  border-left: 2px solid #ffffff;
  animation: typing 3s steps(10) infinite;
}

@keyframes typing {

  40%,
  60% {
    left: calc(100% + 30px);
  }

  100% {
    left: 0;
  }
}

.header_wapp>.header_left>.header_left_content>h1 {
  letter-spacing: 0.4rem;
  line-height: 4rem;
}

.header_wapp>.header_left>.header_left_content>h1>span {
  color: rgb(24, 125, 207);
  letter-spacing: 0.4rem;
  line-height: 3rem;
}

.header_wapp>.header_left>.header_left_content>.contact_btn {
  margin-top: 2rem;
}

.header_wapp>.header_left>.header_left_content>.contact_btn>.btn {
  padding: 0.8rem 2rem;
  background-color: rgb(35, 96, 228);
  border-radius: 0.6rem;
  font-size: 1.5rem;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}

.header_right {
  height: 100%;
  width: 40%;
  /* background-color: rgb(226, 208, 43);   */
  display: flex;
  align-items: center;
  justify-content: center;
}

.header_right>.profile_bg_r {
  /* border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%; */
  border-radius: 50%;
  height: 20rem;
  width: 20rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ffffff;
  position: relative;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px !important;
  margin-bottom: -5rem;
}

.header_wapp>.header_right>.profile_bg_r>img {
  height: 93%;
  width: 93%;
  position: absolute;
  border-radius: 50%;
  user-select: none;
  filter: contrast(110%)
}



